import router from '@/router';

export default {
     loginout(state){
        if(confirm("是否退出登录？")){
            state.islogin = false
            var exp = new Date();
            exp.setTime(exp.getTime() - 1);
            // document.cookie= document.cookie+";expires="+exp.toGMTString(); //删除token
            localStorage.removeItem('token')
            localStorage.removeItem("people"); //删除个人信息
            localStorage.removeItem("islogin"); //未登录状态
            router.push('/')
        }
       },
      loginstate_true(state){
         localStorage.setItem('islogin',JSON.stringify(true))
         state.islogin = JSON.parse(localStorage.getItem('islogin'))
      },
      loginstate_false(state){
         localStorage.setItem('islogin',JSON.stringify(false))
         state.islogin = JSON.parse(localStorage.getItem('islogin'))
      },
       modifypeoplemsg(state,obj){
       localStorage.setItem('people',obj)
       state.people = JSON.parse(localStorage.getItem('people'))
     },
       setpeople(state){
      state.people = JSON.parse(localStorage.getItem('people'))
     },
       remove(){
        localStorage.removeItem("people"); //删除个人信息
     },
       commentmodal_true(state,data){
        state.commentmodalshow =true
        state.commentdata = [data]
     },
      commentmodal_false(state,data){
        state.commentmodalshow =false
        state.commentdata = []
     },
      messagemodal_true(state,data){
      state.messagemodalshow =true
      state.messagedata = [data]
      },
      messagemodal_false(state,data){
         state.messagemodalshow =false
         state.messagedata = []
      },
      getarticle(state,content){
         state.arcitle = content
      },
      removepeopledata(state){
         localStorage.removeItem("people"); //删除个人信息
         state.people = ''
      }
}