import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'


Vue.use(ElementUI) //ui库

Vue.config.productionTip = false


Vue.mixin({
  filters: {
    countLt99 (value) {
      if (value > 99) {
        return '99+'
      } else {
        return value
      }
    }
  }
})



var app = new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

var bus = new Vue()

Vue.prototype.bus = bus

router.beforeEach((to, from, next) => {
  if(localStorage.getItem('token')){
    app.$store.commit('loginstate_true')
  }else{
    app.$store.commit('loginstate_false')
    app.$store.commit('removepeopledata')
  }

  if(to.path === '/mine' && from.path.includes('/mine')){
    app.$router.go(-2)
  }
  next()
})