<template>
  <div class="wiki-home-header-warp">
    <div class="wiki-home-header">
      <router-link tag="a" to="/" class="wiki-home-header-home">魔镜</router-link>

      <div class="wiki-home-header-login-state-no" v-show="!islogin">
        <router-link tag="a" to="/login" class="wiki-home-header-login">登录</router-link>
        <router-link tag="a" to="/register" class="wiki-home-header-register">注册</router-link>
      </div>

      <div class="wiki-home-header-login-state-is" v-if="islogin">
        <el-input v-model="input" suffix-icon="el-icon-search" placeholder="请输入内容"></el-input>
        <el-dropdown :hide-on-click="false">
          <span class="el-dropdown-link">
            <img :src="ava" alt="">
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>
              <router-link tag="li" to="/mine/page"  class="wiki-home-header-login">
                <i class="icon">&#xe6ce;</i>
                个人主页
              </router-link>
            </el-dropdown-item>
            <el-dropdown-item>
              <router-link tag="li" to="/mine/message" class="wiki-home-header-login">
                <i class="icon">&#xe614;</i>
                您的消息
              </router-link>
            </el-dropdown-item>
            <el-dropdown-item>
              <span @click="loginout">
                <i class="icon">&#xe622;</i>
                退出
              </span>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      activeIndex: "1",
      activeIndex2: "1",
      input: "",
      ava:'' //头像
    };
  },
  methods: {
    ...mapMutations(["loginout"])
  },
  computed: {
    ...mapState(["islogin", "people"])
  },
  created() {
    if (localStorage.getItem("token")) {
      this.$store.commit('loginstate_true')
    } else {
      this.$store.commit("loginstate_false");
      this.$store.commit("removepeopledata");
    }
    this.ava = JSON.parse(localStorage.getItem('people')).avatar
  }
};
</script>

<style lang='scss'>
@font-face {
  font-family: 'iconfont';  /* project id 1338628 */
  src: url('//at.alicdn.com/t/font_1338628_79lu9ufonuh.eot');
  src: url('//at.alicdn.com/t/font_1338628_79lu9ufonuh.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_1338628_79lu9ufonuh.woff2') format('woff2'),
  url('//at.alicdn.com/t/font_1338628_79lu9ufonuh.woff') format('woff'),
  url('//at.alicdn.com/t/font_1338628_79lu9ufonuh.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_1338628_79lu9ufonuh.svg#iconfont') format('svg');
}
.icon{
  font-family: 'iconfont';
}

.wiki-home-header-warp {
  width: 100%;
  background-color: rgba(70, 76, 91, 1);
  position: fixed;
  top: 0;
  z-index: 999999;
  .wiki-home-header {
    width: 1280px;
    height: 100px;
    margin: 0 auto;
    background-color: rgba(70, 76, 91, 1);
    display: flex;
    justify-content: space-between;
    .el-input {
      width: 300px;
      height: 50px;
      margin-right: 60px;
      .el-input__inner {
        height: 50px;
      }
      .el-input__icon {
        line-height: 30px;
        font-size: 25px;
      }
    }
    .el-dropdown {
      color: #fff;
      padding-top: 20px;
    }
    .wiki-home-header-login-state-no {
      margin-left: 100px;
      font-size: 20px;
      .wiki-home-header-login {
        margin-right: 10px;
        color: #fff;
        display: inline-block;
        height: 100px;
        line-height: 100px;
      }
      a:hover {
        color: rgb(243, 160, 34);
      }
      .wiki-home-header-register {
        color: #fff;
        display: inline-block;
        height: 50px;
        line-height: 50px;
      }
    }
    .wiki-home-header-home {
      color: #fff;
      display: inline-block;
      height: 100px;
      line-height: 100px;
      margin-right: 110px;
      font-size: 30px;
    }

    .wiki-home-header-login-state-is {
      display: flex;
      line-height: 100px;
      .el-dropdown-link{
        cursor: pointer;
        img{
          width: 50px;
          height: 50px;
        }
      }
    }

    .wiki-home-header-login-out {
      line-height: 100px;
      height: 100px;
      color: #fff;
      margin-left: 10px;
    }
  }
}
</style>
