<template>
  <div class="wiki-home-content-warp">
    <p class="home-content-title">攻防技术矩阵</p>
    <div class="wiki-home-content">
      <ul v-for="(item,index) in  list" :key="index" class="wiki-home-content-list">
        <li>{{item["type"]["type_name"]}}</li>
        <li 
        v-for="(bookslist,index) in  item['books']" 
        :key="index+'1-1'" 
        class="wiki-home-content-list-item"
        :title="bookslist.book_name"
        >
          <router-link tag="a" :to="{path:'/outline', query:{id: bookslist.id,type_name: item.type.type_name,book_name:bookslist.book_name, type_id:item.type.id}}">{{bookslist['book_name']}}</router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import * as ajax from '@/api'

import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      list: [],
    }
  },
  created() {
    this.is = JSON.parse(localStorage.getItem("islogin"));
  },
  computed: {
    ...mapState(["people", "islogin"])
  },
  created(){
    ajax.getHomeList().then(resp=>{
      if(resp.data.code){
        this.list= resp.data.data.all_type_books
      }
    })
  }
};
</script>

<style lang='scss'>
.wiki-home-content-warp{
  margin-top: 100px;
}
.home-content-title{
    height:50px;
    text-align: center;
    font-size: 50px;
    margin-top: 40px;
    color: #808080;
  }
.wiki-home-content {
  min-width: 1280px;
  width: 100%;
  display: flex;
  margin-top: 40px;
  .wiki-home-content-list {
    width: 200px;
    list-style: none;
    height: 100%;
    background-color: #fff;
    border-left: 2px solid #f6f6f6;
    li {
      height: 50px;
      line-height: 50px;
      text-align: center;
      border-bottom: 1px solid #e1e4e5;
      a {
        display: inline-block;
        width: 100%;
        height: 100%;
        color: #4f7cac;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
      }
      a:hover {
        color: red;
        font-size: 12px;
      }
    }
    li:nth-child(1){
      height: 30px;
      background-color: #999;
      line-height: 30px;
      color: #fff;
    }
  }
  .wiki-home-content-list:nth-child(1){
    margin-left: 30px;
  }
  .wiki-home-content-list:nth-last-child(1){
    margin-right: 30px;
  }
